:root {
  --clr-primary-red: hsl(0, 100%, 67%);
  --clr-primary-yellow: hsl(39, 100%, 56%);
  --clr-primary-teal: hsl(166, 100%, 37%);
  --clr-primary-blue: hsl(234, 85%, 45%);
  --clr-light-slate-blue: hsl(252, 100%, 67%); /*(background) */
  --clr-light-royal-blue: hsl(241, 81%, 54%); /*(background) */
  --clr-violet-blue: hsla(256, 72%, 46%, 1); /* (circle) */
  --clr-persian-blue: hsla(241, 72%, 46%, 0); /* (circle) */
  --clr-white: hsl(0, 0%, 100%);
  --clr-pale-blue: hsl(221, 100%, 96%);
  --clr-light-lavender: hsl(241, 100%, 89%);
  --clr-dark-gray-blue: hsl(224, 30%, 27%);
  --ff-sans: "Hanken Grotesk", Arial, Helvetica, sans-serif;
  --ff-mono: "Source Code Pro", monospace;
  --ff-serif: Cambria, Cochin, Georgia, serif;
  --gradient-main: linear-gradient(
    180deg,
    var(--clr-light-slate-blue) 0%,
    var(--clr-light-royal-blue) 100%
  );
  --gradient-circle: linear-gradient(
    180deg,
    var(--clr-violet-blue) 0%,
    var(--clr-persian-blue) 100%
  );
  --fs-4xlarge: 3.815rem;
  --fs-3xlarge: 3.052rem;
  --fs-2xlarge: 2.44125rem;
  --fs-xlarge: 1.953125rem;
  --fs-large: 3.5rem;
  --fs-med: 1.5rem;
  --fs-reg: 1.125rem;
  --fs-small: 1rem;
  --fs-xsmall: 0.6875rem;
  --gap: 1.25rem;
  --columns: 2;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-style: normal;
  font-weight: normal;
}

body {
  font-family: var(--ff-sans);
  font-weight: 500;
  font-size: var(--fs-med);
  line-height: 1.6;
  position: relative;
}
@media (min-width: 64em) {
  body {
    font-size: var(--fs-reg);
  }
}

html,
body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  margin: 0px auto;
  width: 100%;
  box-sizing: border-box;
  background: var(--clr-pale-blue);
}

.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

button {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 3.5rem;
  border: none;
  background: var(--clr-dark-gray-blue);
  color: var(--clr-white);
  border-radius: 8rem;
}

.card {
  border-radius: 0.75rem;
}
.card__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  gap: 1rem;
  margin-bottom: 1.5rem;
}
.card__summary {
  display: flex;
  gap: 0.75rem;
  width: 100%;
  font-size: var(--fs-small);
  border-radius: 0.75rem;
  padding: 1.09375rem;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}
.card__score {
  color: var(--clr-dark-gray-blue);
  margin-left: auto;
}
.card__score span {
  opacity: 0.5;
}
.card--reaction {
  border-radius: 12px;
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.95) 0%, rgba(255, 255, 255, 0.95) 100%), var(--clr-primary-red);
}
.card--reaction .card__title {
  color: var(--clr-primary-red);
}
.card--memory {
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.95) 0%, rgba(255, 255, 255, 0.95) 100%), var(--clr-primary-yellow);
}
.card--memory .card__title {
  color: var(--clr-primary-yellow);
}
.card--verbal {
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.95) 0%, rgba(255, 255, 255, 0.95) 100%), var(--clr-primary-teal);
}
.card--verbal .card__title {
  color: var(--clr-primary-teal);
}
.card--visual {
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.95) 0%, rgba(255, 255, 255, 0.95) 100%), var(--clr-primary-blue);
}
.card--visual .card__title {
  color: var(--clr-primary-blue);
}

.attribution {
  font-size: var(--fs-xsmall);
  text-align: center;
}

.attribution a {
  color: hsl(228, 45%, 44%);
}

.container {
  margin: 0 auto;
  width: 100%;
}
@media (min-width: 64em) {
  .container {
    display: flex;
    background: var(--clr-white);
    border-radius: 2rem;
    height: 32rem;
    width: 46rem;
    margin-bottom: 1.5rem;
  }
  .container .results,
  .container .summary {
    width: 50%;
  }
}

.results {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  gap: 1.5rem;
  padding: 1.5rem 3.59375rem 2.5rem;
  background: var(--gradient-main);
  color: var(--clr-white);
  border-radius: 0 0 2rem 2rem;
  min-height: 22.25rem;
  box-shadow: 0px 30px 60px 0px rgba(61, 108, 236, 0.15);
}
.results__header h1 {
  font-size: var(--fs-reg);
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  color: var(--clr-light-lavender);
  margin: 0;
}
.results__score {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: var(--gradient-circle);
  height: 8.75rem;
  width: 8.75rem;
  border-radius: 100%;
  font-weight: 700;
  font-size: var(--fs-small);
  color: var(--clr-light-lavender);
}
.results__score span {
  font-size: var(--fs-large);
  font-style: normal;
  font-weight: 800;
  line-height: 4.5rem;
  color: var(--clr-white);
}
.results__body h2 {
  font-size: var(--fs-med);
}
.results__body p {
  color: var(--clr-light-lavender);
  font-size: var(--fs-small);
  line-height: normal;
  font-weight: 500;
}
@media (min-width: 64em) {
  .results {
    width: 23rem;
    border-radius: 2rem;
  }
}

.summary {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  align-items: flex-start;
  padding: 1.5rem 1.875rem 1.875rem;
}
.summary__header h2 {
  color: var(--clr-dark-gray-blue);
  font-size: var(--fs-reg);
  font-weight: 700;
  line-height: normal;
  margin-bottom: 1.5rem;
}